$swYellow: #FFFF66;
$swBlue: #4BD5EE;

.container {
  max-width: 1100px;
  margin: 0 auto;
  font-family: "Droid Sans", arial, verdana, sans-serif;
  overflow: auto;

}

.boxcontainer {
  width: 80%;
  float: right;
}

.box {
  float: right;
  border: 1px solid $swYellow;
  border-radius: 5px;
  background: black;
  padding: 30px 5%;
  margin: 10px;
  position: relative;
}

header::after {
  position: absolute;
  top: 15px;
  left: -20px;
  position: absolute;
  content: " ";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 20px 20px 20px 0;
  border-color: transparent #4BD5EE transparent transparent;

}


header {
  font-size: 25px;
  padding: 10px;
  color: $swBlue;
  border-bottom: 1px solid $swBlue;
}

p {
  margin-top: 30px;
  margin-bottom: 30px;
  font-size: 20px;
  color: $swYellow;
}

sidebar {
  width: 20%;
  float: left;
  overflow: auto;
}


